.Card {
  position: relative;
  width: 146px;
  height: 204px;
  border: 1px solid black;
  border-radius: 8px;
  overflow: hidden;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 4px;
}

.Card-art {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}



